<template>
    <div
        v-if="dialog"
        class="confirm-modal"
        @click.self="close()"
    >
        <div class="confirm-modal__dialog">
            <header class="confirm-modal__header">
                <h2>Deadline</h2>
                <span
                    class="confirm-modal__close"
                    @click="close()"
                >
                    <CloseIcon />
                </span>
            </header>
            <section class="confirm-modal__content">
                <div class="action-block">
                    <div>
                        <form-date-picker
                            v-model="$v.duedate.$model"
                            label="Due date"
                            tabindex="0"
                            :min_hours="3"
                            :field.sync="duedate"
                            :errors="validationMessage($v.duedate)"
                            @change.native="$v.duedate.$touch()"
                        />
                    </div>
                    <div class="confirm-modal__alert">
                        <div class="confirm-modal__alert-icon">
                            <AlertCircle />
                        </div>
                        The new deadline must be ahead of the current deadline ({{ getDueHours(due) | moment_short_text }})
                    </div>
                </div>
            </section>
            <footer>
                <div class="confirm-modal__one-button-block">
                    <button
                        class="btn-base btn-base_normal btn-base_colored"
                        @click.prevent="validate(false)"
                    >
                        Ok
                    </button>
                </div>
            </footer>
        </div>
    </div>
</template>

<script>
import { eventBus } from '@/helpers/event-bus'
import filtersMixin from '@/mixins/filtersMixin.js'
import Api from '@/helpers/api/index.js'
import moment from 'moment'

// validation
import { validationMixin } from 'vuelidate'
import formValidationMixin from '@/mixins/formValidationMixin.js'
import { validationMessage } from 'vuelidate-messages'
import { validationRules } from '@/validation/ExtendDeadline/Rules.js'
import { formMessages } from '@/validation/ExtendDeadline/Messages.js'

import CloseIcon from 'mdi-vue/Close.vue';
import AlertCircle from 'mdi-vue/AlertCircle.vue';

export default {
    name: 'DeadlineModal',
    components: {
        CloseIcon,
        AlertCircle
    },
    mixins: [validationMixin, formValidationMixin, filtersMixin],
    validations: {
        ...validationRules
    },
    props: {
        due: { type: String },
        orderid: { type: String }
    },
    data: () => ({
        dialog: false,
        duedate: null
    }),
    mounted() {
        this.duedate = this.due
    },
    methods: {
        validationMessage: validationMessage(formMessages),
        async submit() {
            const data = {
                id: this.orderid,
                datetime: this.duedate
            }
            const vm = this
            Api.post('/api/cp/order/extend-deadline', data)
                .then(() => {
                    eventBus.$emit('showSnackBar', 'The deadline has been extended.', 'success');
                })
                .catch((e) => {
                    eventBus.$emit('showSnackBar', e.response.data.message, 'error');
                })
                .finally(() => {
                    vm.close()
                })
        },
        open() {
            this.dialog = true
            return new Promise((resolve, reject) => {
                this.resolve = resolve
                this.reject = reject
            })
        },
        close() {
            this.resolve(false)
            this.dialog = false
        },
        getDueHours(duedate) {
            const due = moment(duedate)
            return due.add(3, 'hours');
        }
    }
}
</script>

<style lang="scss">
    .confirm-modal__alert{
        display: flex;
        color: #fff;
        background-color: #2196f3;
        border-color: #2196f3;
        padding: 16px;
        margin-top: 20px;
        border-radius: 4px;
        &-icon{
            margin-right: 20px;
        }
    }
</style>
