                        <b>
                            {{ activeTab+1 }} of {{ tabs.length }}
                        </b>
                        {{ tabs[activeTab].title }}
                    </p>
                    <div class="feedback_modal__body-rating_text">
                        <form-textarea
                            v-model="$v.message.$model"
                            :legend="tabs[5].question"
                            type="text"
                            :name="tabs[5].question"
                            :field.sync="message"
                            :errors="validationMessage($v.message)"
                            @input.native="$v.message.$touch();"
                            @blur.native="$v.message.$touch();"
                        />
