                />
            </div>

            <!-- footer buttons -->
            <footer class="feedback_modal__btns">
                <button
                    :disabled="activeTab === 0"
                    depressed
                    block
                    class="btn-base btn-base_normal btn-base_border"
                    @click.prevent="previousStep()"
                >
                    Back
                </button>

                <button
                    v-if="activeTab < 5"
                    :disabled="activeTab === 5 || !isCurrentStepValid"
                    text
                    block
                    class="btn-base btn-base_normal btn-base_colored feedback_modal__send"
                    @click.prevent="nextStep(tabs[activeTab].name)"
                >
                    Next
                </button>

                <!-- final button -->
                <button
                    v-else
                    :disabled="!isCurrentStepValid"
                    class="btn-base btn-base_normal btn-base_colored feedback_modal__send"
                    @click.prevent="validate(false)"
                >
