<script>
import { eventBus } from '@/helpers/event-bus'
import Api from '@/helpers/api/index.js'

import { validationMixin } from 'vuelidate'
import formValidationMixin from '@/mixins/formValidationMixin'
import { validationMessage } from 'vuelidate-messages'
import { validationRules } from '@/validation/feedback/Rules'
import { formMessages } from '@/validation/feedback/Message'

import CloseIcon from 'mdi-vue/Close.vue';
import Barcode from 'mdi-vue/Barcode';

import Avatar from '@/components/common/Avatar.vue'
import StarRating from 'vue-star-rating'

export default {
    validations: {
        ...validationRules
    },
    components: {
        CloseIcon,
        Avatar,
        StarRating,
        Barcode
    },
    mixins: [
        validationMixin,
        formValidationMixin
    ],
    props: {
        order: {
            type: Object
        }
    },
    data: () => ({
        dialog: false,
        activeTab: 0,
        tabs: [
            {
                active: false,
                name: 'Delivery',
                title: 'Was the project delivered on time?',
                question: 'Describe your problem',
                details_min: 20,
                details_max: 200
            },
            {
                active: false,
                name: 'Content',
                title: 'Was the content of good quality?',
                question: 'Describe your problem'
            },
            {
                active: false,
                name: 'Communication',
                title: 'Did the writer communicate well?',
                question: 'Describe your problem'
            },
            {
                active: false,
                name: 'Website',
                title: 'How would you rate our website?',
                question: 'Describe your problem'
            },
            {
                active: false,
                name: 'Support',
                title: 'Was our support team helpful?',
                question: 'Describe your problem'
            },
            {
                active: false,
                name: 'Message',
                title: 'Please tell us about your overall experience',
                question: 'Message'
            }
        ],
        delivery: null,
        delivery_details: '',
        content: null,
        content_details: '',
        communication: null,
        communication_details: '',
        website: null,
        website_details: '',
        support: null,
        support_details: '',
        message: ''
    }),
    computed: {
        progress() {
            const calculatedProgress = ((this.activeTab) / this.tabs.length) * 100
            return calculatedProgress
        },
        isCurrentStepValid() {
            switch (this.activeTab) {
            case 0:
                return !this.$v.delivery.$invalid && !this.$v.delivery_details.$invalid
            case 1:
                return !this.$v.content.$invalid && !this.$v.content_details.$invalid
            case 2:
                return !this.$v.communication.$invalid && !this.$v.communication_details.$invalid
            case 3:
                return !this.$v.website.$invalid && !this.$v.website_details.$invalid
            case 4:
                return !this.$v.support.$invalid && !this.$v.support_details.$invalid
            case 5:
                return !this.$v.message.$invalid
            default:
                return false
            }
        }
    },
    watch: {
        delivery(val) {
            if (+val >= 4) {
                this.delivery_details = ''
            }
        },
        content(val) {
            if (+val >= 4) {
                this.content_details = ''
            }
        },
        communication(val) {
            if (+val >= 4) {
                this.communication_details = ''
            }
        },
        website(val) {
            if (+val >= 4) {
                this.website_details = ''
            }
        },
        support(val) {
            if (+val >= 4) {
                this.support_details = ''
            }
        }
    },
    methods: {
        validationMessage: validationMessage(formMessages),
        open() {
            this.dialog = true
            return new Promise((resolve, reject) => {
                this.resolve = resolve
                this.reject = reject
            })
        },
        nextStep(name) {
            console.log(this.$v);
            this.$v.$reset()
            if ((name === 'Delivery') && !this.$v.delivery.$invalid && !this.$v.delivery_details.$invalid) {
                this.activeTab += 1
            } else if (name === 'Content' && !this.$v.content.$invalid && !this.$v.content_details.$invalid) {
                this.activeTab += 1
            } else if (name === 'Communication' && !this.$v.communication.$invalid && !this.$v.communication_details.$invalid) {
                this.activeTab += 1
            } else if (name === 'Website' && !this.$v.website.$invalid && !this.$v.website_details.$invalid) {
                this.activeTab += 1
            } else if (name === 'Support' && !this.$v.support.$invalid && !this.$v.support_details.$invalid) {
                this.activeTab += 1
            } else {
                this.$v.$touch()
                eventBus.$emit('showSnackBar', 'Please complete the form', 'error');
            }
        },
        previousStep() {
            if (this.activeTab > 0) {
                this.activeTab -= 1
            }
        },
        async submit() {
            const vm = this
            vm.loading = true

            const data = {
                delivery: this.delivery,
                delivery_details: this.delivery_details,
                content: this.content,
                content_details: this.content_details,
                communication: this.communication,
                communication_details: this.communication_details,
                website: this.website,
                website_details: this.website_details,
                support: this.support,
                support_details: this.support_details,
                message: this.message,
                orderid: this.order.orderid
            }

            Object.entries(data).forEach(([key, value]) => {
                if (value === '') {
                    delete data[key]
                }
            });

            new Promise(() => {
                const vm = this;
                Api.post('/api/testimonials/new-testimonial', data)
                    .then(() => {
                        eventBus.$emit('showSnackBar', 'Your feedback has been submited. Thank you!', 'success')
                        vm.close()
                    }).catch((error) => {
                        console.log(error);
                        eventBus.$emit('showSnackBar', error, 'error');
                    })
            })
        },
        clearAllFields() {
            this.$v.$reset()
            this.delivery = null
            this.delivery_details = ''
            this.content = null
            this.content_details = ''
            this.communication = null
            this.communication_details = ''
            this.website = null
            this.website_details = ''
            this.support = null
            this.support_details = ''
            this.message = ''
            this.activeTab = 0
        },
        close() {
            this.resolve(false)
            this.clearAllFields()
            this.dialog = false
        }
    }
}
</script>

