<template>
    <div
        v-if="dialog"
        class="confirm-modal"
        @click.self="close()"
    >
        <div class="confirm-modal__dialog">
            <header class="confirm-modal__header">
                <h2>Revision Request</h2>
                <p class="modal__header-description">
                    {{ orderid }}: &laquo;{{ title }}&raquo;
                </p>
                <span
                    class="confirm-modal__close"
                    @click="close()"
                ><CloseIcon /></span>
            </header>
            <section class="confirm-modal__content">
                <form-textarea
                    v-model="$v.message.$model"
                    legend="Revision request"
                    type="text"
                    name="Revision request"
                    :field.sync="message"
                    :errors="validationMessage($v.message)"
                    @input.native="$v.message.$touch();"
                    @blur.native="$v.message.$touch();"
                />
                <div class="confirm-modal__alert">
                    <div class="confirm-modal__alert-icon">
                        <AlertCircle />
                    </div>
                    You will be taken to the order form which will be populated with the order details. You still need to choose the deadline.
                </div>
            </section>
            <footer>
                <div class="confirm-modal__one-button-block">
                    <button
                        class="confirm-modal__one-button confirm-modal__one-button--main"
                        @click="validate(false)"
                    >
                        ok
                    </button>
                </div>
            </footer>
        </div>
    </div>
</template>

<script>
import { validationMixin } from 'vuelidate'
import formValidationMixin from '@/mixins/formValidationMixin'
import { validationMessage } from 'vuelidate-messages'

import { validationRules } from '@/validation/Revision/Rules'
import { formMessages } from '@/validation/Revision/Messages'

import CloseIcon from 'mdi-vue/Close.vue';
import AlertCircle from 'mdi-vue/AlertCircle.vue';

import Api from '@/helpers/api/index.js'
import { eventBus } from '@/helpers/event-bus/'

export default {
    name: 'ReportModal',
    components: {
        CloseIcon,
        AlertCircle
    },
    mixins: [
        validationMixin,
        formValidationMixin
    ],
    validations: {
        ...validationRules
    },
    props: {
        orderid: {
            type: String
        },
        title: {
            type: String
        }
    },
    data() {
        return {
            dialog: false,
            message: ''
        }
    },
    methods: {
        open() {
            this.dialog = true
            return new Promise((resolve, reject) => {
                this.resolve = resolve
                this.reject = reject
            })
        },
        close() {
            this.resolve(false)
            this.dialog = false
        },
        validationMessage: validationMessage(formMessages),
        async submit() {
            const vm = this
            Api.post('/api/cp/order-message/create-revision', {
                orderid: vm.orderid,
                message: vm.message
            }).then(() => {
                eventBus.$emit('showSnackBar', 'Your comment has been saved', 'success')
                vm.dialog = false
                vm.message = ''
            })
        }
    }
}
</script>

<style lang="scss" scoped>
    .confirm-modal{
        &__header{
            padding-right: 50px;
        }
    }
</style>
