<template>
    <div
        v-if="dialog"
        class="confirm-modal"
        @click.self="close()"
    >
        <div class="confirm-modal__dialog">
            <header class="confirm-modal__header">
                <h2 class="modal__header-title">
                    Order extra pages
                </h2>
                <p class="modal__header-description">
                    {{ orderid }}
                </p>
                <span
                    class="confirm-modal__close"
                    @click="close()"
                >
                    <CloseIcon />
                </span>
            </header>
            <section class="confirm-modal__content">
                <div class="action-block">
                    <div class="confirm-modal__alert">
                        <div class="confirm-modal__alert-icon">
                            <AlertCircle />
                        </div>
                        You will be taken to the order form which will be populated with the order details. You still need to choose the deadline.
                    </div>
                </div>
            </section>
            <footer>
                <div class="confirm-modal__one-button-block">
                    <a
                        class="btn-base btn-base_normal btn-base_colored"
                        @click.prevent="goToOrderForm"
                    >
                        Proceed
                    </a>
                </div>
            </footer>
        </div>
    </div>
</template>

<script>
import CloseIcon from 'mdi-vue/Close.vue';
import AlertCircle from 'mdi-vue/AlertCircle.vue';

export default {
    components: {
        CloseIcon,
        AlertCircle
    },
    props: {
        orderid: {
            type: String
        }
    },
    data: () => ({
        dialog: false
    }),
    methods: {
        open() {
            this.dialog = true
            return new Promise((resolve, reject) => {
                this.resolve = resolve
                this.reject = reject
            })
        },
        close() {
            this.resolve(false)
            this.dialog = false
        },
        goToOrderForm() {
            this.loading = true
            this.close()
            this.$router.push({
                name: 'order_new',
                query: {
                    linked_to_order: this.orderid
                }
            })
            this.loading = false
        }
    }
}
</script>

<style lang="scss">
    .confirm-modal__alert{
        display: flex;
        color: #fff;
        background-color: #2196f3;
        border-color: #2196f3;
        padding: 16px;
        margin-top: 20px;
        border-radius: 4px;
        &-icon{
            margin-right: 20px;
        }
    }
</style>
