<template>
    <div class="orders-card">
        <div class="orders-card__info">
            <router-link
                v-if="order.title"
                :to="{
                    name: 'details',
                    params: {
                        id: order.orderid,
                        page: page
                    },
                    query: (() => (!order.files.length ? { tab: 'files' } : null))()
                }"
                :class="{ 'orders-card__info-default': mode === 'details' }"
            >
                <h2 class="orders-card__title">
                    <span
                        v-if="mode === 'list'"
                        class="orders-card__title-index"
                    >
                        {{ offset + index + 1 }}.
                    </span>
                    «<span class="order-title-text">{{ order.title }}</span>»
                    <span class="break-xs">
                        <span
                            class="orders-card__title--status"
                            :class="{
                                'orders-card__title--status-success':
                                    order.status === 'PAID' || order.status === 'COMPLETE' || order.status === 'CREDIT',
                                'orders-card__title--status-attention': order.status === 'PENDING' || order.status === 'REVISION',
                                'orders-card__title--status-error':
                                    order.status === 'REFUND' || order.status === 'RETURN' || order.status === 'UNPAID'
                            }"
                        >
                            {{ order.status }}
                        </span>
                    </span>
                </h2>
            </router-link>

            <div
                class="orders-card__row animation"
                :class="{ animation: order.showTools }"
            >
                <div class="orders-card__row-left orders-card__row-left-avatar">
                    <span
                        v-if="order.writer"
                        class="writer-in-order__avatar"
                    >
                        <avatar
                            :avatar="order.writer.avatar"
                            :firstname="order.writer.firstname"
                            :lastname="order.writer.lastname"
                        />
                    </span>

                    <div
                        v-if="order.writer"
                        class="writer-in-order__naming"
                    >
                        <span
                            v-if="order.writer.sw_id.length"
                            class="writer-in-order__writer-id"
                        >
                            {{ 'ID: ' + order.writer.sw_id }}
                        </span>
                        <span class="writer-in-order__writer-nic">{{ getWriterTitle(order) }}</span>
                    </div>
                </div>

                <div class="orders-card__row-right">
                    <!-- Deadline -->
                    <span
                        v-if="order.status === `PENDING` || order.status === `UNPAID` || order.status === `REVISION`"
                        class="orders-card__detail-info"
                    >
                        <span class="orders-card__detail-info-title"> Deadline </span>
                        <span class="orders-card__detail-info-counter orders-card__detail-info-counter-deadline">
                            <img
                                src="@/assets/img/svg/clock.svg"
                                alt="Deadline"
                                width="20px"
                                height="20px"
                                class="card-icon"
                            >
                            in {{ order.due_at_local | moment_from }}
                        </span>
                    </span>

                    <!-- files counter -->
                    <span
                        v-if="order.files"
                        :class="{ 'file-redirect__off': mode === 'details' }"
                        class="orders-card__detail-info orders-card__detail-info-redirect"
                        @click.prevent="redirectToOrderFiles(order.orderid)"
                    >
                        <span class="orders-card__detail-info-title">Files</span>
                        <span class="orders-card__detail-info-counter">
                            <img
                                src="@/assets/img/svg/file.svg"
                                alt="Deadline"
                                width="20px"
                                height="20px"
                                class="card-icon"
                            >
                            {{ order.files.length || 'Add' }}
                        </span>
                    </span>

                    <!-- Messages -->
                    <span
                        v-if="order.messages"
                        class="orders-card__detail-info orders-card__detail-info-redirect"
                        @click.prevent="redirectToOrderMessages(order.orderid)"
                    >
                        <span class="orders-card__detail-info-title">Messages</span>
                        <span class="orders-card__detail-info-counter">
                            <img
                                src="@/assets/img/svg/message-square.svg"
                                alt="Deadline"
                                width="20px"
                                height="20px"
                                class="card-icon"
                            >
                            {{ order.messages.length || 'Add' }}
                            <span
                                v-if="order.unread_messages.length > 0"
                                class="file__new"
                            >
                                {{ order.unread_messages.length }}
                            </span>
                        </span>
                    </span>

                    <!-- Total -->
                    <span class="orders-card__detail-info orders-card__detail-info--md0">
                        <span class="orders-card__detail-info-title">Total</span>
                        <span class="orders-card__detail-info-counter">
                            <img
                                src="@/assets/img/svg/credit-card.svg"
                                alt="Deadline"
                                width="20px"
                                height="20px"
                                class="card-icon"
                            >
                            {{ order.total | money }}
                        </span>
                    </span>

                    <!-- Arrow -->
                    <a
                        href="#"
                        class="orders-card__toggle-btn"
                        @click.prevent="toggle(order)"
                    >
                        <ChevronUpIcon v-if="order.showTools" />
                        <ChevronDownIcon v-else />
                    </a>
                </div>
            </div>

            <transition name="slide-y-transition">
                <div
                    v-if="order.showTools"
                    class="orders-card__more-info"
                >
                    <div
                        class="orders-card__row"
                        :class="{ animation: order.showTools }"
                    >
                        <div class="orders-card__row-left">
                            <span
                                v-if="order.due_at_local"
                                class="orders-card__detail-info"
                            >
                                <span class="orders-card__detail-info-title"> Order ID </span>
                                <span class="orders-card__detail-info-counter">
                                    <img
                                        src="@/assets/img/svg/clipboard.svg"
                                        alt="Deadline"
                                        width="20px"
                                        height="20px"
                                        class="card-icon"
                                    >
                                    {{ order.orderid }}
                                </span>
                            </span>

                            <span
                                v-if="order.due_at_local"
                                class="orders-card__detail-info"
                            >
                                <span class="orders-card__detail-info-title">
                                    Ordered: {{ order.due_at_local | moment_from }} ago
                                </span>
                                <span class="orders-card__detail-info-counter">
                                    <img
                                        src="@/assets/img/svg/clock.svg"
                                        alt="Deadline"
                                        width="20px"
                                        height="20px"
                                        class="card-icon"
                                    >
                                    {{ order.due_at_local | moment_short_text }}
                                </span>
                            </span>

                            <span
                                v-if="order.due_at_local"
                                class="orders-card__detail-info orders-card__detail-info--md0"
                            >
                                <span class="orders-card__detail-info-title">
                                    Due: in {{ order.due_at_local | moment_from }}
                                </span>
                                <span class="orders-card__detail-info-counter">
                                    <img
                                        src="@/assets/img/svg/chevrons-right.svg"
                                        alt="Deadline"
                                        width="20px"
                                        height="20px"
                                        class="card-icon"
                                    >
                                    {{ order.due_at_local | moment_short_text }}
                                </span>
                            </span>

                            <span
                                v-if="order.linked_to_order"
                                class="orders-card__detail-info mr-5 extra"
                            >
                                <barcode />
                                <router-link
                                    :to="{
                                        name: 'details',
                                        params: {
                                            id: order.linked_to_order,
                                            page: page
                                        }
                                    }"
                                >
                                    {{ order.linked_to_order }}
                                </router-link>

                                <span class="animation__info"> Extra pages for order ID </span>
                            </span>
                        </div>
                    </div>

                    <div
                        v-if="order.testimonials.length"
                        class="orders-card__testimonials"
                    >
                        <div
                            v-for="(testimonial, testimonialsIndex) in order.testimonials"
                            :key="testimonialsIndex"
                            class="orders-card__testimonials-item"
                        >
                            <avatar
                                :avatar="order.writer.avatar"
                                :firstname="order.writer.firstname"
                                :lastname="order.writer.lastname"
                            />
                            <div class="orders-card__testimonials-item__right">
                                <div class="orders-card__testimonials-item__right-name">
                                    Rated
                                    <span v-if="testimonial.nickname">{{ testimonial.nickname }}</span>
                                    <span v-else>{{ `Writer ` + testimonial.sw_id }} </span>
                                    {{ getRating(testimonial) }}
                                </div>
                                <star-rating
                                    :rating="getRating(testimonial)"
                                    :read-only="true"
                                    :increment="0.1"
                                    :star-size="20"
                                    :show-rating="false"
                                />
                            </div>
                        </div>
                    </div>

                    <div class="orders-card__row">
                        <FooterActions
                            :data="order"
                            @actionCompleted="onActionCompleted"
                            @Deleted="deleted()"
                        />

                        <router-link
                            v-if="mode === 'list'"
                            :to="{
                                name: 'details',
                                params: {
                                    id: order.orderid,
                                    page: page
                                }
                            }"
                            class="btn-base btn-base_normal btn-base_border orders-card__more-link"
                        >
                            More Info
                        </router-link>
                    </div>
                </div>
            </transition>

            <a
                href="#"
                class="orders-card__toggle-btn_mobile"
                @click.prevent="toggle(order)"
            >
                <span
                    v-if="order.showTools"
                    class="btn-base btn-base_normal btn-base_border btn-base_border--initial btn-base_border-ticket"
                >hide</span>

                <span
                    v-else
                    class="btn-base btn-base_normal btn-base_border btn-base_border-ticket"
                >Show more</span>
            </a>
        </div>
    </div>
</template>

<script>
import Avatar from '@/components/common/Avatar.vue'
import filtersMixin from '@/mixins/filtersMixin.js'
import { scroller } from 'vue-scrollto/src/scrollTo'

// Icons
import Barcode from 'mdi-vue/Barcode';
import ChevronDownIcon from 'mdi-vue/ChevronDown'
import ChevronUpIcon from 'mdi-vue/ChevronUp'

import FooterActions from '@/components/account/FooterActions'
import StarRating from 'vue-star-rating'

export default {
    components: {
        Barcode,
        ChevronDownIcon,
        ChevronUpIcon,
        FooterActions,
        Avatar,
        StarRating
    },
    filters: {
        userFriendlyNaming(value) {
            switch (value) {
            case 'UNPAID':
                return 'UNPAID'
            case 'PAID':
                return 'PAIN'
            case 'PENDING':
                return 'IN PROGRESS'
            case 'COMPLETE':
                return 'COMPLETE'
            case 'REVISION':
                return 'REVISION'
            case 'REFUND':
                return 'REFUND'
            case 'RETURN':
                return 'RETURN'
            case 'CREDIT':
                return 'CREDIT'
            default:
                return value
            }
        }
    },
    mixins: [filtersMixin],
    props: {
        order: {
            type: Object,
            required: true
        },
        index: {
            type: Number
        },
        offset: {
            type: Number
        },
        page: {
            type: Number
        },
        mode: {
            type: String
        }
    },
    data: () => ({}),
    methods: {
        toggle(order) {
            const vmOrder = order
            vmOrder.showTools = !order.showTools
            this.$forceUpdate()
        },
        async onActionCompleted(value) {
            this.$emit('updateData', value)
        },
        async deleted(value) {
            this.$emit('onDeleted', value)
        },
        getRating(data) {
            let rating = (Number(data.delivery) + Number(data.content) + Number(data.communication)) / 3
            rating = rating.toFixed(2)
            return +rating
        },
        redirectToOrderMessages(id) {
            if (this.$route.hash !== '#messages') {
                this.$router.push({
                    name: 'details',
                    params: {
                        id
                    },
                    hash: '#messages'
                })
            }
            this.scrollTo()
        },
        scrollTo() {
            const el = document.querySelector('.orders-details-tabs')
            if (el) {
                const firstScrollTo = scroller();
                firstScrollTo(el, {
                    duration: 600,
                    offset: -120
                });
            }
        },
        redirectToOrderFiles(id) {
            if (this.$route.hash !== '#files') {
                this.$router.push({
                    name: 'details',
                    params: {
                        id
                    },
                    hash: '#files'
                })
            }
            this.scrollTo()
        },
        getWriterTitle(model) {
            if (model.sw_id.length > 0 && model.writer.nickname.length > 0) return model.writer.nickname
            if (model.sw_id.length > 0 && !model.writer.nickname) return `Writer ${model.sw_id}`
            return 'No writer assigned'
        }
    }
}
</script>
