<template>
    <div class="orders-card__footer-actions">
        <footer class="list-item-footer">
            <!-- footer actions -->

            <!-- delete -->
            <a
                v-if="allowDelete"
                href="#"
                class="orders-card__footer-link"
                @click.prevent="deleteOrder(data.orderid)"
            >
                <span class="orders-card__footer-link-delete" />
                Delete
            </a>
            <!-- delete -->

            <!-- feedback -->
            <a
                v-if="allowFeedback"
                href="#"
                class="orders-card__footer-link"
                @click.prevent="feedback"
            >
                <span class="orders-card__footer-link-star" />
                Rate Writer {{ data.sw_id }}
                <span class="orders-card__footer-link-money" />
                <span class="custom-tooltip">
                    <p>?</p>
                    <span>
                        Select the type of project that most closely describes your assignment
                        (i.e. do not choose “essay”, if you have a complex research paper or report)
                    </span>
                </span>
            </a>
            <!-- feedback -->

            <!-- deadline -->
            <a
                v-if="allowDeadline"
                href="#"
                class="orders-card__footer-link orders-card__footer-link--mdi"
                @click.prevent="deadline"
            >
                <span class="orders-card__footer-link-clock" />
                Extend deadline
            </a>
            <!-- deadline -->

            <!-- extra pages -->
            <a
                v-if="allowExtraPages"
                href="#"
                class="orders-card__footer-link"
                @click.prevent="extraPages"
            >
                <span class="orders-card__footer-link-file" />
                Order Extra Pages
            </a>
            <!-- extra pages -->

            <!-- revision -->
            <a
                v-if="allowRevision"
                href="#"
                class="orders-card__footer-link"
                @click.prevent="revision"
            >
                <span class="orders-card__footer-link-conversation" />
                Revision
            </a>
            <!-- revision -->
        </footer>

        <confirm-modal ref="delete-modal" />
        <feedback-modal
            ref="feedback-modal"
            :order="data"
        />
        <deadline-modal
            ref="deadline-modal"
            :due="data.due_at_local"
            :orderid="data.orderid"
        />
        <revision-modal
            ref="revision-modal"
            :orderid="data.orderid"
            :title="data.title"
        />
        <extra-pages-modal
            ref="extrapages-modal"
            :orderid="data.orderid"
        />
    </div>
</template>

<script>
import Api from '@/helpers/api/index.js'
import { eventBus } from '@/helpers/event-bus'

import ConfirmModal from '@/components/modals/ConfirmModal';
import FeedbackModal from '@/components/modals/FeedbackModal';
import DeadlineModal from '@/components/modals/DeadlineModal';
import ExtraPagesModal from '@/components/modals/ExtraPagesModal';
import RevisionModal from '@/components/modals/RevisionModal';

export default {
    components: {
        ConfirmModal,
        FeedbackModal,
        DeadlineModal,
        ExtraPagesModal,
        RevisionModal
    },
    props: {
        data: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            loading: false
        }
    },
    computed: {
        allowExtraPages() {
            const array = [
                'PAID',
                'PENDING',
                'COMPLETE',
                'REVISION'
            ]
            return array.includes(this.data.status)
        },
        allowFeedback() {
            const array = [
                'COMPLETE',
                'REFUND',
                'CREDIT'
            ]
            if (!this.data.writer) return false

            if (array.indexOf(this.data.status) === -1) return false

            return true
        },
        allowDelete() {
            const array = [
                'UNPAID'
            ]
            return array.includes(this.data.status)
        },
        allowFinalize() {
            const array = [
                'UNPAID'
            ]
            return array.includes(this.data.status)
        },
        allowDeadline() {
            const array = [
                'PAID',
                'REVISION',
                'PENDING'
            ]
            return array.includes(this.data.status)
        },
        allowRevision() {
            const array = [
                'COMPLETE'
            ]
            return array.includes(this.data.status)
        },
        confirmer() {
            return this.$store.state.confirmer
        }
    },
    watch: {
        async page() {
            await this.getData()
        }
    },
    methods: {
        emitBackToParent(value) {
            this.$emit('actionCompleted', value)
        },
        async feedback() {
            this.$refs['feedback-modal'].open()
        },
        finalize() {
            this.$store.dispatch('order/order_form/ACTION_FINALIZE_BATCH', {

            })
        },
        revision() {
            this.$refs['revision-modal'].open()
        },
        async deadline() {
            this.$refs['deadline-modal'].open()
        },
        extraPages() {
            this.$refs['extrapages-modal'].open()
        },

        async deleteOrder(orderid) {
            const modalTitle = 'Delete'
            const modalText = `Are you sure you want to delete order ${orderid}`
            if (await this.$root.$confirm(modalTitle, modalText, { color: 'red' })) {
                Api.post('/api/cp/order/delete-unpaid', {
                    id: this.data.orderid
                })
                    .then(() => {
                        eventBus.$emit('showSnackBar', `Order ${this.data.orderid} has been deleted!`, 'success');
                        eventBus.$emit('DeletedOrder', this.data.orderid);
                    })
                    .catch((error) => {
                        console.log(error)
                    })
            }
        },
        confirm(method, id) {
            if (method === 'delete') {
                this.delete(id)
            }
        },
        async delete(id) {
            const vm = this
            try {
                await this.deleteOrder({
                    id
                })
                vm.$emit('Deleted', `Order ${id} has been deleted!`)
                eventBus.$emit('showSnackBar', `Order ${id} has been deleted!`, 'success');
            } catch (e) {
                console.log(e)
            }
        }
    }
}
</script>
